@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: "Poppins",sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*{
  margin: 0px;
  padding: 0px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding:2.5rem 1.5rem;
}

.app__searchForm{
  margin:1.2rem;
}

.app__input{
  padding:0.3rem .9rem; 
  min-width:200px;
  max-width: 100vw;
  font-size: 1rem;
}

.app__submit{
  padding:0.3rem .9rem; 
  margin-left: .3rem;
  background-color: #02aff0;
  color:white;
  border-radius:0.3rem;
  border:none;
  transition:all 0.4s ease-in-out;

}

.app__submit:hover{
  background-color: #fff;
  color:#02aff0;
  border:1px solid #02aff0; 
  outline:none; 
}
.app_recipes{
  margin-top: 24px;
  display: grid;
  grid-template-columns: 230px 230px;
  
}

.recipeTitle__img{
    height:200px;
    width:200px;
    object-fit:cover;
    border-radius:4px;
}

.recipeTitle__name{
    max-width: 200px;
    text-align: center;
    margin:12px 0px;
}


